.intro__wrapper {
  display: flex;
}
@media (max-width: 1199.5px) {
  .intro__wrapper {
    justify-content: space-between;
  }
}
@media (max-width: 767.5px) {
  .intro__wrapper {
    justify-content: space-around;
  }
}

.intro__description {
