@media (max-width: 1199.5px) {
  .intro__description {
    width: 52%;
    margin-right: 30px;
    padding-top: 57px;
    padding-left: 0;
  }
}
@media (max-width: 767.5px) {
  .intro__description {
    width: 100%;
